	<title>Home - Csaba Palfi</title>
</svelte:head>

<h1>Csaba Palfi</h1>

<ul>
	{#each posts as post}
		<li><a rel='prefetch' href='{post.slug}'>{post.title}</a></li>
	{/each}
</ul>